// probably better if moved to wrapper.js.
import React from 'react'
import styled from 'styled-components'
import { Navbar, Nav } from 'react-bootstrap';


const Header = styled.header`
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999;
  width: 100%;
`

export default class NavSideBar extends React.Component {
    constructor(props) {
        super(props)

        this.state = {
            show: false
        }
        this.clickHandler = this.clickHandler.bind(this)
        this.toggleMenu = this.toggleMenu.bind(this)
    }

    toggleMenu() {
        this.setState({
            show: !this.state.show
        })
    }

    clickHandler() {
        const element = document.getElementById('slide-panel');
        element.style.animation = 'none';
        setTimeout(function () {
            element.style.animation = '';
        }, 5);
        element.classList.add('slide-out');
    }

    render () {
        return (
                <Navbar>
                    <Nav className="flex-column">
                    <Nav.Link to='/about' href="/about" onClick={this.clickHandler}>
                                <h3>About Me</h3>
                            </Nav.Link>
                            <Nav.Link to='/professional' href="/professional">
                                <h3>Professional</h3>
                            </Nav.Link>
                            <Nav.Link to='/education' href="/education">
                                <h3>Education</h3>
                            </Nav.Link>
                            <Nav.Link to='/portfolio' href="/portfolio">
                                <h3>Portfolio</h3>
                            </Nav.Link>
                        </Nav>
                </Navbar>

        )
    }
}