import React from 'react'

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import {Nav, Navbar, NavbarBrand, NavDropdown} from "react-bootstrap";

import { library } from '@fortawesome/fontawesome-svg-core'
import { faFolderOpen as fasFaFolderOpen , faAddressCard as fasFaAddressCard, faGraduationCap as fasFaGraduationCap, faUserTie as fasFaUserTie} from '@fortawesome/free-solid-svg-icons'
import { faFolderOpen as farFaFolderOpen , faAddressCard as farFaAddressCard} from '@fortawesome/free-regular-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'

import '../config/Transitions.css'
import './css/Wrapper.css'
import {MainHeading} from "../config/Typography";

const PAGE_TO_ICON = Object.freeze({
   'ABOUT' : <FontAwesomeIcon icon={['fas', 'address-card']} />,
    'PROFESSIONAL' : <FontAwesomeIcon icon={['fas', 'user-tie']} />,
    'PORTFOLIO' : <FontAwesomeIcon icon={['fas', 'folder-open']} />,
    'EDUCATION' : <FontAwesomeIcon icon={['fas', 'graduation-cap']} />,
});

library.add(farFaFolderOpen, fasFaFolderOpen, farFaAddressCard, fasFaGraduationCap, fasFaUserTie, fasFaAddressCard)

export default class Wrapper extends React.Component {
    clickHandler() {
        const element = document.getElementById('slide-panel');
        element.style.transition = 'none';
        setTimeout(function () {
            element.style.transition = '';
        }, 5);
        element.classList.add('slide-out');
    }
    setNavExpanded(expanded) {
        this.setState({ navExpanded: expanded });
        this.forceUpdate();
    }
    updateWidth() {
        this.setState({ screenWidth: window.innerWidth });
    }
    isSmall() {
        return this.state.screenWidth < 768 && this.state.screenWidth > 0;
    }
    getBanner() {
        return this.state.screenWidth < 768 ? (
            <Navbar sticky="top"  >
                <NavbarBrand>
                        <MainHeading> {PAGE_TO_ICON[this.props.currentPage]} About Ash </MainHeading>
                </NavbarBrand>

                <Navbar.Toggle aria-controls="responsive-navbar-nav" />
                <Navbar.Collapse id="responsive-navbar-nav">
                    <Nav className="mr-auto">
                        <NavDropdown title="Menu" alignRight style={{fontSize:'1.75rem', marginLeft: 'auto', marginRight: '0'}} id="collasible-nav-dropdown">
                            <NavDropdown.Item href={process.env.PUBLIC_URL + '/'}>About</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href={process.env.PUBLIC_URL + '/professional'}>Professional</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href={process.env.PUBLIC_URL + '/education'}>Education</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href={process.env.PUBLIC_URL + '/portfolio'}>Portfolio</NavDropdown.Item>

                        </NavDropdown>
                    </Nav>
                </Navbar.Collapse>
            </Navbar>
        ) : (
            <Navbar onToggle={this.setNavExpanded} expanded={this.state.navExpanded} >
                <NavbarBrand>
                    <MainHeading> {PAGE_TO_ICON[this.props.currentPage]} About Ash </MainHeading>
                </NavbarBrand>
            </Navbar>
        );
    }

    getSideBar() {
        return (this.state.screenWidth >= 768)
            ? (

                    <Navbar sticky="top" >
                            <Nav activeKey={window.location.pathname} affix className="flex-column">
                                <Nav.Link exact activeClassName="navbar__link--active"
                                          className="navbar__link" href={process.env.PUBLIC_URL + '/'} >
                                    <h3>About</h3>
                                </Nav.Link>
                                <Nav.Link activeClassName="navbar__link--active"
                                          className="navbar__link" href={process.env.PUBLIC_URL + '/professional'} >
                                    <h3>Professional</h3>
                                </Nav.Link>
                                <Nav.Link activeClassName="navbar__link--active"
                                          className="navbar__link" href={process.env.PUBLIC_URL + '/education'}>
                                    <h3>Education</h3>
                                </Nav.Link>
                                <Nav.Link activeClassName="navbar__link--active"
                                          className="navbar__link" href={process.env.PUBLIC_URL + '/portfolio'}>
                                    <h3>Portfolio</h3>
                                </Nav.Link>
                            </Nav>
                    </Navbar>

            ) : '';
    }
    constructor(props) {
        super(props);
        this.clickHandler = this.clickHandler.bind(this)
        this.setNavExpanded = this.setNavExpanded.bind(this)
        this.updateWidth = this.updateWidth.bind(this)
        this.getBanner = this.getBanner.bind(this)
        this.getSideBar = this.getSideBar.bind(this)
        this.isSmall = this.isSmall.bind(this)
        this.state = {
            screenWidth: -1,
        }
    }
    componentDidMount() {
        this.updateWidth();
        window.addEventListener('resize', this.updateWidth);
    }
    componentWillUnmount() {
        window.removeEventListener('resize', this.updateWidth);
    }
    render() {
        return (
            <div className={'wrapper-style'} style={{backgroundColor: this.props.backgroundColor}}>
                <Container>
                    <Row>
                        <Col md={12}>
                            <div className="page-header">
                                {this.getBanner()}
                            </div>
                        </Col>
                    </Row>
                    <Row id="slide-panel">
                        <Col>{this.props.children}</Col>
                        <Col md="auto">
                            {this.getSideBar()}
                        </Col>
                    </Row>
                </Container>
            </div>
        );
    }
}